@import url(https://fonts.googleapis.com/css2?family=Jost:wght@300;400;600&display=swap);
* {
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
}

html {
  /* min-height: 100%; */
  overflow-x: hidden;
}

body {
  font-family: 'Jost', sans-serif;;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #EBEBEB;
  display: flex;
  align-items: center;
  justify-content: center;
  /* position: fixed; */
  min-height: 100vh;
  width: 100vw;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  cursor: pointer;
}

#root {
  width: 100vw;
  min-height: 100vh;
  display: flex;
  justify-content: center;
}

.login-page {
  align-self: center;
  background-color: #f8f9fa;
  border-radius: 25px;
  height: 600px;
  width: 1250px;
}

.login-container {
  position: relative;
  width: 50%;
}

.login-container .logo {
  position: absolute;
  top: 30px;
}

.login-form {
  width: 70%;
}

.unvalidated-input:focus {
  border-color: #fe8686;
  box-shadow: 0 0 0 0.25rem rgb(253 13 13 / 25%);
}

.validated-input:focus {
  border-color: #349f35;
  box-shadow: 0 0 0 0.25rem rgb(32 145 11 / 25%);
}

.coin-image {
  background-color: gainsboro;
  border-bottom-right-radius: 25px;
  border-top-right-radius: 25px;
  display: flex;
  width: 50%;
}

.coin-image img {
  filter: drop-shadow(2px 4px 6px rgba(0, 0, 0, 0.1));
  width: 400px;
}

@media screen and ( min-width : 1440px ) {

  .login-page {
    height: 800px;
    width: 1350px;
  }

  .login-container .logo {
    position: absolute;
    top: 40px;
  }

  .coin-image img {
    width: 500px;
  }
}

@media screen and ( max-width : 1280px ) {

  .login-page {
    width: 900px;
  }

  .login-container .logo {
    width: 80%;
  }

  .coin-image img {
    width: 80%;
  }

  .login-container .login-p {
    font-size: 25px;
  }
}

@media screen and ( max-width : 920px ) {

  .login-page {
    width: 90vw;
  }
}

@media screen and ( max-width : 768px ) {

  .coin-image {
    display: none;
  }

  .login-container {
    width: 100%;
  }

  .login-page {
    height: 500px;
  }

  .login-form {
    margin-top: 100px;
  }
}

@media screen and ( max-width : 520px ) {

  .login-page {
    height: 420px;
  }

  .login-form {
    margin-top: 80px;
  }
}

.wallet-page {
  background-color: #f8f9fa;
  min-height: 100vh;
  width: 100vw;
}

.wallet-page .logo img {
  padding: 0 15px;
}

.wallet-left-column {
  background-color: #212529;
  color: #f8f9fa;
  padding-bottom: 30px;
  position: relative;
  transition: 500ms background;
  width: 320px;
}

.column-editing {
  background-color: #2b3238;
  transition: 500ms background;
}

.wallet-page .line {
  background-color: white;
  height: 1px;
  margin-bottom: 2rem;
  width: 80%;
}

.email-field {
  font-size: 12px;
}

.expenses {
  font-size: 35px;
  margin-top: -0.8rem;
}

.form-expense {
  width: 80%;
}

.fields {
  width: 100%;
}

.right-column {
  flex: 1 1;
  min-width: calc(100vw - 320px);
  width: auto;
}

.expenses-table {
  height: 100%;
  overflow-x: scroll;
  overflow-y: scroll;
  width: 100%;
}

.cell-buttons button {
  background: none;
  border: none;
  margin: 0 0.3rem;
  padding: 0;
}

thead th {
  font-size: 12px;
}

tbody {
  font-size: 15px;
}

.expand-btn {
  background-color: #495661;
  border: none;
  border-bottom-left-radius: 5px;
  color: #eee;
  font-size: 8px;
  padding: 4px 8px;
  position: absolute;
  right: 0;
}

.wallet-left-column.close {
  animation: close 200ms ease-in-out;
  width: 50px;
}

.wallet-left-column.open {
  animation: open 200ms ease-in-out;
}

.wallet-left-column.close * {
  display: none;
}

.wallet-left-column.close .expand-btn {
  display: block;
}

@keyframes close {

  from {
    width: 320px;
  }

  to {
    width: 50px;
  }
}

@keyframes open {

  from {
    width: 50px;
  }

  to {
    width: 320px;
  }
}

@media screen and ( min-width : 1440px ) {

  .form-expense {
    height: 450px;
    justify-content: space-between;
  }

  .fields {
    height: 400px;
    justify-content: space-around;
  }

  .fields button {
    margin: 0;
  }

}

